___CSS_LOADER_EXPORT___.push([module.id, ".rag-red{background-color:#ec333880!important}.rag-green{background-color:#6dbe4f80!important}.rag-yellow{background-color:#fbb22f80!important}.rag-green-arrived{background-color:#00c079!important}.rag-gray{background-color:#a9a9a9!important}.rag-on-repair{background-color:#a020f045!important}.ag-header-cell-menu-button{display:none}div.ag-header-cell-label{justify-content:center;text-align:center}", "",{"version":3,"sources":["webpack://./resources/js/app/components/ServerSideTable.vue"],"names":[],"mappings":"AA6kBA,SACI,oCACJ,CACA,WACI,oCACJ,CACA,YACI,oCACJ,CACA,mBACI,kCACJ,CACA,UACI,kCACJ,CACA,eACI,oCACJ,CACA,4BACI,YACJ,CACA,yBAEI,sBAAuB,CADvB,iBAEJ","sourcesContent":["<template>\n    <div>\n        <!-- Loading indicator -->\n        <div v-if=\"isLoading\" class=\"loading-overlay\">\n            <div class=\"spinner\"></div>\n        </div>\n\n        <!-- AG Grid -->\n        <ag-grid-vue\n            style=\"height: 70vh; width: 100%\"\n            class=\"ag-theme-quartz\"\n            :columnDefs=\"columnDefs\"\n            :defaultColDef=\"defaultColDef\"\n            :rowData=\"rowData\"\n            :pagination=\"false\"\n            :columnTypes=\"columnTypes\"\n            :rowClassRules=\"rowClassRules\"\n            :tooltipShowDelay=\"tooltipShowDelay\"\n            :tooltipHideDelay=\"tooltipHideDelay\"\n            :context=\"{ componentParent: this }\"\n            @grid-ready=\"onGridReady\"\n            @cell-clicked=\"onCellClicked\"\n            @filter-changed=\"onFilterChanged\"\n            @sort-changed=\"onSortChanged\"\n        >\n        </ag-grid-vue>\n\n        <!-- Custom Pagination -->\n        <div class=\"pagination-container\">\n            <div class=\"pagination-info\">\n                Showing {{ startRecord }} - {{ endRecord }} of {{ totalRecords }} records\n            </div>\n            <div class=\"pagination-controls\">\n                <button\n                    class=\"pagination-btn\"\n                    :disabled=\"currentPage === 1\"\n                    @click=\"goToPage(1)\"\n                >\n                    &laquo;\n                </button>\n                <button\n                    class=\"pagination-btn\"\n                    :disabled=\"currentPage === 1\"\n                    @click=\"goToPage(currentPage - 1)\"\n                >\n                    &lsaquo;\n                </button>\n\n                <template v-for=\"page in visiblePages\" :key=\"page\">\n                    <button\n                        v-if=\"page === '...'\"\n                        class=\"pagination-btn pagination-ellipsis\"\n                        disabled\n                    >\n                        ...\n                    </button>\n                    <button\n                        v-else\n                        class=\"pagination-btn\"\n                        :class=\"{ active: page === currentPage }\"\n                        @click=\"goToPage(page)\"\n                    >\n                        {{ page }}\n                    </button>\n                </template>\n\n                <button\n                    class=\"pagination-btn\"\n                    :disabled=\"currentPage === lastPage\"\n                    @click=\"goToPage(currentPage + 1)\"\n                >\n                    &rsaquo;\n                </button>\n                <button\n                    class=\"pagination-btn\"\n                    :disabled=\"currentPage === lastPage\"\n                    @click=\"goToPage(lastPage)\"\n                >\n                    &raquo;\n                </button>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport \"ag-grid-community/styles/ag-grid.css\";\nimport \"ag-grid-community/styles/ag-theme-quartz.css\";\nimport { AgGridVue } from \"ag-grid-vue3\";\n\nfunction alterRendererWithView(params) {\n    let eGui = document.createElement(\"div\");\n\n    const modelType =\n        params.context?.componentParent?.setting?.model || \"responses\";\n\n    const permissions = {\n        responses: {\n            view: \" \",\n            edit: \" \",\n            delete: \" \",\n        },\n        repairs: {\n            view: \" \",\n            edit: \" \",\n            delete: \" \",\n        },\n    };\n\n    const modelPermissions = permissions[modelType] || permissions[\"responses\"];\n\n    let buttonsHtml = \"\";\n\n    if ($can(modelPermissions.view)) {\n        buttonsHtml += `<i style=\"cursor:pointer; color:green; font-size:1.2em; margin-right:0.3em;\" data-action=\"view\" class=\"fas fa-eye\"></i>`;\n    }\n\n    if ($can(modelPermissions.edit)) {\n        buttonsHtml += `<i style=\"cursor:pointer; color:green; font-size:1.2em; margin-right:0.3em;\" data-action=\"edit\" class=\"fas fa-edit\"></i>`;\n    }\n\n    if ($can(modelPermissions.delete)) {\n        buttonsHtml += `<i style=\"cursor:pointer; color:red; font-size:1.2em;\" data-action=\"delete\" class=\"fas fa-trash\"></i>`;\n    }\n\n    eGui.innerHTML = buttonsHtml;\n    return eGui;\n}\n\nfunction $can(permissionName) {\n    return Laravel.jsPermissions.permissions.indexOf(permissionName) !== -1;\n}\n\nexport default {\n    props: [\"user\", \"additional\", \"name\", \"setting\"],\n    name: \"server-side-table\",\n    components: { AgGridVue },\n    data: () => {\n        return {\n            rowData: [],\n            gridApi: null,\n            columnApi: null,\n            rowClassRules: null,\n            isLoading: false,\n            currentPage: 1,\n            lastPage: 1,\n            totalRecords: 0,\n            perPage: 17,\n            currentFilters: {},\n            currentSort: { field: 'id', order: 'desc' },\n            filterDebounceTimer: null,\n        };\n    },\n    computed: {\n        startRecord() {\n            if (this.totalRecords === 0) return 0;\n            return (this.currentPage - 1) * this.perPage + 1;\n        },\n        endRecord() {\n            const end = this.currentPage * this.perPage;\n            return end > this.totalRecords ? this.totalRecords : end;\n        },\n        visiblePages() {\n            const pages = [];\n            const total = this.lastPage;\n            const current = this.currentPage;\n\n            if (total <= 7) {\n                for (let i = 1; i <= total; i++) {\n                    pages.push(i);\n                }\n            } else {\n                pages.push(1);\n\n                if (current > 3) {\n                    pages.push('...');\n                }\n\n                let start = Math.max(2, current - 1);\n                let end = Math.min(total - 1, current + 1);\n\n                if (current <= 3) {\n                    end = 4;\n                }\n                if (current >= total - 2) {\n                    start = total - 3;\n                }\n\n                for (let i = start; i <= end; i++) {\n                    pages.push(i);\n                }\n\n                if (current < total - 2) {\n                    pages.push('...');\n                }\n\n                pages.push(total);\n            }\n\n            return pages;\n        }\n    },\n    setup(props) {\n        const actionColumn = {\n            headerName: \"\",\n            headerClass: \"text-center\",\n            maxWidth: 120,\n            filter: false,\n            sortable: false,\n            cellStyle: { textAlign: \"center\" },\n            cellRenderer: alterRendererWithView,\n            editable: false,\n            colId: \"action\",\n        };\n\n        return {\n            defaultColDef: {\n                flex: 1,\n                minWidth: 150,\n                filter: true,\n                floatingFilter: true,\n                sortable: true,\n                resizable: true,\n            },\n            tooltipShowDelay: 1000,\n            tooltipHideDelay: 3000,\n            columnDefs: [...props.setting.columns, actionColumn],\n            columnTypes: {\n                nonEditableColumn: { editable: false },\n                dateColumn: {\n                    filter: \"agDateColumnFilter\",\n                    filterParams: {\n                        comparator: (filterLocalDateAtMidnight, cellValue) => {\n                            const dateAsString = cellValue;\n                            if (dateAsString == null) {\n                                return 0;\n                            }\n                            const dateParts = dateAsString.split(\"/\");\n                            const month = Number(dateParts[0]) - 1;\n                            const day = Number(dateParts[1]);\n                            const year = Number(dateParts[2].split(\" \")[0]);\n                            const cellDate = new Date(year, month, day);\n\n                            if (cellDate < filterLocalDateAtMidnight) {\n                                return -1;\n                            } else if (cellDate > filterLocalDateAtMidnight) {\n                                return 1;\n                            }\n                            return 0;\n                        },\n                    },\n                },\n            },\n        };\n    },\n    mounted() {\n        this.rowClassRules = {\n            \"rag-on-repair\": \"data.on_repair == 1\",\n            \"rag-green-arrived\": \"data.status == 10 || data.status == 5\",\n            \"rag-green\": \"data.status == 3\",\n            \"rag-red\": \"data.status == 1\",\n            \"rag-yellow\": \"data.status == 2\",\n            \"rag-gray\": \"data.status == 9\",\n        };\n    },\n    methods: {\n        async fetchData() {\n            this.isLoading = true;\n\n            try {\n                const params = {\n                    page: this.currentPage,\n                    perPage: this.perPage,\n                    sortField: this.currentSort.field,\n                    sortOrder: this.currentSort.order,\n                };\n\n                if (Object.keys(this.currentFilters).length > 0) {\n                    params.filters = JSON.stringify(this.currentFilters);\n                }\n\n                const response = await this.$http.get(this.setting.url.request.index, { params });\n\n                this.rowData = response.data.data;\n                this.totalRecords = response.data.total;\n                this.currentPage = response.data.page;\n                this.lastPage = response.data.lastPage;\n\n            } catch (error) {\n                console.error('Error fetching data:', error);\n                this.$toast.error('  ', {\n                    position: \"top-right\",\n                    duration: 5000,\n                });\n            } finally {\n                this.isLoading = false;\n            }\n        },\n\n        goToPage(page) {\n            if (page < 1 || page > this.lastPage || page === this.currentPage) {\n                return;\n            }\n            this.currentPage = page;\n            this.fetchData();\n        },\n\n        onGridReady(params) {\n            this.gridApi = params.api;\n            this.columnApi = params.columnApi;\n\n            // Expose grid API to window for export functionality\n            if (this.setting.model === 'responses') {\n                window.responsesGridApi = params.api;\n            } else if (this.setting.model === 'services') {\n                window.servicesGridApi = params.api;\n            } else if (this.setting.model === 'repairs') {\n                window.repairsGridApi = params.api;\n            }\n\n            this.fetchData();\n        },\n\n        onFilterChanged(params) {\n            // Clear existing timer\n            if (this.filterDebounceTimer) {\n                clearTimeout(this.filterDebounceTimer);\n            }\n\n            // Debounce filter changes\n            this.filterDebounceTimer = setTimeout(() => {\n                const filterModel = this.gridApi.getFilterModel();\n                this.currentFilters = {};\n\n                // Map AG Grid filter model to our API format\n                for (const [field, filter] of Object.entries(filterModel)) {\n                    // Find the column definition to get the actual field name\n                    const colDef = this.setting.columns.find(col => col.field === field);\n                    let apiField = field;\n\n                    // Extract field from valueGetter if it exists\n                    if (colDef && colDef.valueGetter) {\n                        const match = colDef.valueGetter.match(/data\\.(.+)/);\n                        if (match) {\n                            apiField = match[1];\n                        }\n                    }\n\n                    this.currentFilters[apiField] = {\n                        filter: filter.filter,\n                        filterType: filter.filterType || 'text',\n                        type: filter.type || 'contains'\n                    };\n                }\n\n                this.currentPage = 1;\n                this.fetchData();\n            }, 300);\n        },\n\n        onSortChanged(params) {\n            const sortModel = this.gridApi.getColumnState()\n                .filter(col => col.sort)\n                .map(col => ({\n                    colId: col.colId,\n                    sort: col.sort\n                }));\n\n            if (sortModel.length > 0) {\n                const colDef = this.setting.columns.find(col => col.field === sortModel[0].colId);\n                let sortField = sortModel[0].colId;\n\n                // Extract field from valueGetter if it exists\n                if (colDef && colDef.valueGetter) {\n                    const match = colDef.valueGetter.match(/data\\.(.+)/);\n                    if (match) {\n                        sortField = match[1];\n                    }\n                }\n\n                this.currentSort = {\n                    field: sortField,\n                    order: sortModel[0].sort\n                };\n            } else {\n                this.currentSort = { field: 'id', order: 'desc' };\n            }\n\n            this.currentPage = 1;\n            this.fetchData();\n        },\n\n        removeRequest(id, setting, callback) {\n            let token = document\n                .querySelector('meta[name=\"csrf-token\"')\n                .getAttribute(\"content\");\n\n            return this.$http\n                .delete(\n                    setting.url.request.destroy.replace(\"__delete__\", id),\n                    { id },\n                    {\n                        \"Content-Type\": \"application/json\",\n                        \"X-CSRF-TOKEN\": token,\n                    }\n                )\n                .then(async (response) => {\n                    if (response.data.success == true) {\n                        callback();\n                    } else {\n                        if (response.data.errs.length)\n                            response.data.errs.map((item) =>\n                                this.$toast.error(item, {\n                                    position: \"top-right\",\n                                    duration: 7000,\n                                })\n                            );\n                    }\n                })\n                .catch(function (e) {\n                    if (e.response && e.response.statusText)\n                        this.$toast.error(e.response.statusText, {\n                            position: \"top-right\",\n                            duration: 7000,\n                        });\n                });\n        },\n\n        remove(params, id) {\n            return Swal.fire({\n                title: ' ?',\n                text: \"   !\",\n                icon: 'warning',\n                showCancelButton: true,\n                confirmButtonColor: '#3085d6',\n                cancelButtonColor: '#d33',\n                confirmButtonText: ', !',\n                cancelButtonText: ''\n            }).then((result) => {\n                if (result.isConfirmed) {\n                    this.removeRequest(id, this.setting, () => {\n                        this.fetchData();\n                        Swal.fire(\n                            '!',\n                            '  .',\n                            'success'\n                        );\n                    });\n                }\n            });\n        },\n\n        onCellClicked(params) {\n            if (params.column.colId == \"action\") {\n                let action = params.event.target.dataset.action;\n\n                if (action === \"edit\") {\n                    let redirect = this.setting.url.request.edit.replace(\n                        \"new\",\n                        params.data.id\n                    );\n                    window.location.href = redirect;\n                } else if (action == \"view\") {\n                    let redirect = this.setting.url.request.show.replace(\n                        \"new\",\n                        params.data.id\n                    );\n                    window.location.href = redirect;\n                } else if (action === \"delete\") {\n                    this.remove(params, params.data.id);\n                }\n            }\n        },\n    },\n};\n</script>\n\n<style scoped>\n.loading-overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background: rgba(255, 255, 255, 0.7);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    z-index: 1000;\n}\n\n.spinner {\n    width: 40px;\n    height: 40px;\n    border: 4px solid #f3f3f3;\n    border-top: 4px solid #3498db;\n    border-radius: 50%;\n    animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n    0% { transform: rotate(0deg); }\n    100% { transform: rotate(360deg); }\n}\n\n.pagination-container {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 15px 10px;\n    background: #f8f9fa;\n    border-top: 1px solid #dee2e6;\n    flex-wrap: wrap;\n    gap: 10px;\n}\n\n.pagination-info {\n    color: #6c757d;\n    font-size: 14px;\n    white-space: nowrap;\n}\n\n.pagination-controls {\n    display: flex;\n    gap: 3px;\n    flex-wrap: wrap;\n    justify-content: center;\n}\n\n.pagination-btn {\n    padding: 8px 12px;\n    border: 1px solid #dee2e6;\n    background: white;\n    cursor: pointer;\n    border-radius: 4px;\n    font-size: 14px;\n    transition: all 0.2s;\n    min-width: 36px;\n    text-align: center;\n}\n\n.pagination-btn:hover:not(:disabled) {\n    background: #e9ecef;\n}\n\n.pagination-btn:disabled {\n    opacity: 0.5;\n    cursor: not-allowed;\n}\n\n.pagination-btn.active {\n    background: #007bff;\n    color: white;\n    border-color: #007bff;\n}\n\n.pagination-ellipsis {\n    border: none;\n    background: transparent;\n}\n\n@media (max-width: 576px) {\n    .pagination-container {\n        flex-direction: column;\n        align-items: stretch;\n    }\n\n    .pagination-info {\n        text-align: center;\n        font-size: 12px;\n    }\n\n    .pagination-controls {\n        justify-content: center;\n        overflow-x: auto;\n        max-width: 100%;\n        padding: 5px 0;\n    }\n\n    .pagination-btn {\n        padding: 6px 10px;\n        font-size: 12px;\n        min-width: 32px;\n        flex-shrink: 0;\n    }\n}\n</style>\n\n<style>\n.rag-red {\n    background-color: #ec333880 !important;\n}\n.rag-green {\n    background-color: #6dbe4f80 !important;\n}\n.rag-yellow {\n    background-color: #fbb22f80 !important;\n}\n.rag-green-arrived {\n    background-color: #00c079 !important;\n}\n.rag-gray {\n    background-color: #a9a9a9 !important;\n}\n.rag-on-repair {\n    background-color: #a020f045 !important;\n}\n.ag-header-cell-menu-button {\n    display: none;\n}\ndiv.ag-header-cell-label {\n    text-align: center;\n    justify-content: center;\n}\n</style>\n"],"sourceRoot":""}]);
